import React from "react";
import {Link} from 'react-router-dom'
import posed from 'react-pose';

import profilePicture from '../profilePic.png'
import '../index.css'
import '../index.js'
import { useSpring, animated } from 'react-spring'
import { config } from "@fortawesome/fontawesome-svg-core";

export const Container = posed.div({
  enter: { staggerChildren: 50 },
  exit: { staggerChildren: 20, staggerDirection: -1}
});

export const Div = posed.div({
  enter: { x: 0, opacity: 1 },
  exit: { x: 50, opacity: 0 }
});

export const H3 = posed.h3({
  enter: { x: 0, opacity: 1 },
  exit: { x: 50, opacity: 0 }
});

export const Li = posed.li({
  enter: { x: 0, opacity: 1 },
  exit: { x: 50, opacity: 0 }
});

export const H1 = posed.h1({
  enter: { x: 0, opacity: 1 },
  exit: { x: 50, opacity: 0 }
});

export const H5 = posed.h5({
  enter: { x: 0, opacity: 1 },
  exit: { x: 50, opacity: 0 }
});

export const Image = posed.img({
  enter: { x: 0, opacity: 1 },
  exit: { x: 50, opacity: 0 }
});

export const P = posed.p({
  enter: { x: 0, opacity: 1 },
  exit: { x: 50, opacity: 0 }
});


function Homepage(props) {
  const values = useSpring({
    to: { opacity: 1 },
    from: { opacity: 0 },
    delay: 300,
    reset: false,
    config: config.molasses
  });
  return (
    <div>
      <animated.div style={values} className="d-flex flex-column align-items-center center">
        { /* profile picture */ }
        <Image src={profilePicture} alt="profilePic"/>
        {/* Figure out a way to center the image in css */ }
        <H3 className="text-center text-white font-weight-normal mt-4"> {props.name} </H3>
        <H5 className="text-center text-white font-weight-normal"> {props.occupation} </H5>
        { /* add buttons that connect to the router*/ }
        <div className="buttons">
          <Link to='/aboutme'>
            <button type="button" className="btn mr-2 btn-outline-light">About Me</button>
          </Link>
          <Link to='/contact'>
            <button type="button" className="btn mr-2 btn-outline-light">Contact</button>
          </Link>
          <Link to='/projects'>
            <button type="button" className="btn btn-outline-light">Projects</button>
          </Link>
        </div>
      </animated.div>
    </div>
  )
}

export default Homepage;
