import React from 'react'
import { HashRouter as Router, Route, Switch } from 'react-router-dom'
import posed, { PoseGroup } from 'react-pose';
import About from './aboutme'
import Contact from './contact'
import Projects from './projects'
import Homepage from './homepage'
import '../index.css'

// our animations setup
const RouteContainer = posed.div({
  enter: {
    opacity: 1,
    delay: 500,
    beforeChildren: true,
  },
  exit: {
    opacity: 0
   }
});

const RedirectPage = () => (
  <div className="mx-auto text-white" style={{
    margin: 'auto',
    display: 'block',
    textAlign: 'center',
    paddingTop: '17rem'
  }}>
    <h1> Redirecting </h1>
  </div>
)

const Navigation = () => (
  <Router>
    <Route
      render={({ location }) => (
        <PoseGroup>
          <RouteContainer key={location.pathname}>
            <Switch location={location}>
              {/* Internal links */}
              <Route exact path="/" render={() => <Homepage name="Thanusun Thayaparan" occupation="Full Stack Developer"/>}/>
              <Route path='/aboutme' component={About}/>
              <Route path='/contact' component={Contact}/>
              <Route path='/projects' component={Projects}/>

              {/* External Links; Project related*/}
              <Route path='/netcon' component={() => {
                window.location.replace('https://devpost.com/software/netcon-hb6dki');
                return (
                  <RedirectPage/>
                )
               }
              }/>
              <Route path='/spotbot' component={() => {
                window.location.replace('https://devpost.com/software/guide_bot-lxmecq');
                return (
                  <RedirectPage/>
                )
               }
             }/>
            <Route path='/websitecodepage' component={() => {
              window.location.replace('https://github.com/coolmanbat2/coolmanbat2.github.io');
              return (
                <RedirectPage/>
              )
            }
            }/>
            <Route path='/github' component={() => {
              window.location.replace('https://github.com/coolmanbat2/');
              return (
                <RedirectPage/>
              )
            }
            }/>
            <Route path='/linkedin' component={() => {
              window.location.replace('https://www.linkedin.com/in/thanusun/');
              return (
                <RedirectPage/>
              )
            }
            }/>
            <Route path='/instagram' component={() => {
              window.location.replace('https://www.instagram.com/ryekg/');
              return (
                <RedirectPage/>
              )
            }
            }/>
          </Switch>
        </RouteContainer>
      </PoseGroup>
      )}
    />
  </Router>
)

export default Navigation
